.write {
  padding-top: 50px;
}

.writeImg {
  margin-left: 150px;
  width: 70vw;
  height: 250px;
  border-radius: 10px;
  object-fit: cover;
}

.writeForm {
  position: relative;
}

.writeFormGroup {
  margin-left: 150px;
  display: flex;
  align-items: center;
}

.writeIcon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: gray;
  cursor: pointer;
}
.writeIcon:hover {
  color: #068af0;
}

.writeInput {
  font-size: 30px;
  border: none;
  padding: 20px;
  width: 70vw;
}

.writeInput:focus {
  outline: none;
}

.writeText {
  font-size: 20px;
  height: 100vh;
}

.writeSubmit {
  position: absolute;
  top: 20px;
  right: 50px;
  color: white;
  background-color: #1e262a;
  padding: 8px 30px;
  border: none;
  text-transform: uppercase;
  border-radius: 5px;
  line-height: normal;
  cursor: pointer;
  font-size: 0.8rem;
  letter-spacing: 0.3em;
}
.writeSubmit:hover {
  background-color: #068af0;
  border-radius: 1px solid #068af0;
}
