.top {
  width: 100%;
  height: 50px;
  background-color: white;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  font-family: "Josefin Sans", sans-serif;
  z-index: 999;
}

.topLeft,
.topRight {
  flex: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topCenter {
  flex: 6;
}

.facebook {
  font-size: 20px;
  margin-right: 10px;
  /* color: blue; */
  color: #666;
  cursor: pointer;
}
.facebook:hover {
  color: blue;
}

.twitter {
  font-size: 20px;
  margin-right: 10px;
  color: #666;
  cursor: pointer;
}
.twitter:hover {
  color: #00acee;
}

.pinterest {
  font-size: 20px;
  margin-right: 10px;
  color: #666;
  cursor: pointer;
}
.pinterest:hover {
  color: #bd081c;
}

.instagram {
  margin-right: 10px;
  color: #666;

  font-size: 20px;
  cursor: pointer;
}
.instagram:hover {
  background: radial-gradient(
    circle at 30% 107%,
    #fdf497 0%,
    #fdf497 5%,
    #fd5949 45%,
    #d6249f 60%,
    #285aeb 90%
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.topImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
}
.topListItem:hover {
  background-color: whitesmoke;
  color: #00acee;
}

.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
  margin-left: 15px;
}
.topSearchIcon:hover {
  transform: scale(0.75) translate(-16%, 0);
  color: #664aff;
}
