.contact {
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(
        rgba(255, 255, 255, 0.5),
        rgba(255, 255, 255, 0.5)
      ),
      url("https://www.krioskitchens.com/media/catalog/category/lshape.jpg");
    background-size: cover;
  }
  
  .contactTitle{
      font-size: 50px;
  }

  .contactForm{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}

.contactForm>label{
    margin: 10px 0;
}

.contactInput{
    padding: 10px;
    background-color: white;
    border: none;
}

.contactMessage{
        padding: 50px;
        background-color: white;
        border: none;
}

.contactButton{
    margin-top: 20px;
    cursor: pointer;
    background-color: gray;
    color: black;
    width: 150px;
    align-self: center;
    padding: 8px 30px;
    border: none;
    border-radius: 10px;
    font-size: 2.5vh;
}
.contactButton:hover{
    background-color: #068af0;
    color: white;
}